/*
 * Styles for all pages
 */

::selection {
  background-color: #000;
  color: #fff;
}

body {
  font: normal 14px/16px "font", Helvetica, Arial, sans-serif;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  outline: none;
}

#main-container {
  position: relative;
  margin-top: 56px;
}

@media (min-width: 544px) {
  #main-container {
    position: relative;
    margin-top: 56px;
  }
}

.table {
  position: relative;
  display: table;
  width: 100%;
  height: 100%;
}

.table-cell {
  position: relative;
  display: table-cell;
  vertical-align: middle;
}

.page-header {
  margin: 0;
  padding: 0;
  border: 0;
}

/*------------------------------------*   HEADER
\*------------------------------------*/

/*
 * Header styles
 */

#navbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 56px;
  background: #fff;
  border-bottom: 1px solid #DADADA;
  z-index: 55;
}

#navbar #nav-btn {
  position: absolute;
  top: 15px;
  left: 20px;
  width: 30px;
  height: 22px;
  cursor: pointer;
  z-index: 57;
}

#navbar .nav-btn-bar {
  position: absolute;
  width: 30px;
  height: 3px;
  background: #000;
}

#navbar #nav-btn-bar-1 {
  top: 0;
}

#navbar #nav-btn-bar-2 {
  top: 9px;
}

#navbar #nav-btn-bar-3 {
  top: 18px;
}

#navbar #navbar-label {
  position: relative;
  font-size: 14px;
  line-height: 56px;
  padding-left: 70px;
  z-index: 56;
}

#nav-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 35;
  display: none;
}

@media (min-width: 544px) {
  #nav-overlay {
    display: none;
  }
}

#nav {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 80vw;
  background: #fff;
  z-index: 50;
  padding: 0;
  display: none;
}

@media (min-width: 544px) {
  #nav {
    right: 0;
    bottom: initial;
    width: auto;
    height: 56px;
    transform: none;
    border-bottom: 1px solid #DADADA;
    display: block;
  }

  #nav #nav-container {
    height: 56px;
  }
}

@media (min-width: 544px) {
  .navbar-toggleable-md .navbar-collapse {
    display: flex !important;
    width: 100%;
  }

  .navbar-toggleable-md > .container {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  .navbar-toggleable-md .navbar-nav {
    flex-direction: row;
  }

  .navbar-toggleable-md .navbar-nav .nav-link {
    padding-right: .5rem;
    padding-left: .5rem;
  }
}

.navbar-nav {
  padding: 80px 15px 10px;
}

@media (min-width: 544px) {
  .navbar-nav {
    padding: 0;
  }
}

/*------------------------------------*   FOOTER
\*------------------------------------*/

/*
 * Footer styles
 */

/*------------------------------------*   LOADING
\*------------------------------------*/

/*
 * Styles for loading
 */

#loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: white;
  z-index: -1;
}

#loading-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 150px;
  margin: -75px 0 -200px;
}

/*------------------------------------*  ICONS CREDITS
\*------------------------------------*/

/*
 * Font icon for credits (Rezo Zero / Roadiz)
 */

@font-face {
  font-family: "basethemecredits";
  src: url("../fonts/basethemecredits.eot");
  src: url("../fonts/basethemecredits.eot") format("embedded-opentype"), url("../fonts/basethemecredits.woff") format("woff"), url("../fonts/basethemecredits.ttf") format("truetype"), url("../fonts/basethemecredits.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "basethemecredits" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="credits-icon-"]:before,
[class*=" credits-icon-"]:before {
  font-family: "basethemecredits" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.credits-icon-rezozero:before {
  content: "a";
}

.credits-icon-roadiz:before {
  content: "b";
}

