\*------------------------------------*/

/*
 * Styles for "page" node-type
 */

/*------------------------------------*   MAP BLOCK
\*------------------------------------*/

/*
 * Styles for 'mapblock'
 */

.block-map-canvas {
  width: 100%;
  height: 400px;
}

/*------------------------------------*   DEBUG PANEL
\*------------------------------------*/

#rz-debug-panel {
  font-size: 10px;
  box-sizing: border-box;
  background-color: #EFEFEF;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 9999;
  height: 40px;
  width: 40px;
  border-radius: 10px 0 0 0;
  font-family: Helvetica, Arial, sans-serif;
}

#rz-debug-panel:before {
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
  margin: 6px 4px 4px 6px;
  opacity: 0.2;
  float: left;
  background-image: url("data:image/svg+xml;base64,");
}

#rz-debug-panel h4 {
  display: inline-block;
  font-size: 11px;
  padding: 5px 5px;
  margin: 9px 0px 0px;
  text-transform: uppercase;
  font-weight: bold;
  box-sizing: border-box;
  letter-spacing: 0.04em;
  color: #aba7a7;
  text-shadow: 0px 1px 0px white;
}

#rz-debug-panel ul {
  padding: 0;
  margin: 0;
  display: none;
  clear: both;
}

#rz-debug-panel li {
  list-style: none;
  overflow: hidden;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#rz-debug-panel dl,
#rz-debug-panel dt,
#rz-debug-panel dd {
  display: inline-block;
  padding: 3px 5px;
  margin: 0px;
  box-sizing: border-box;
}

#rz-debug-panel dd {
  padding-left: 0px;
}

#rz-debug-panel .chart-bar {
  background: #85D267;
  height: 3px;
  min-width: 1px;
  position: relative;
}

#rz-debug-panel .chart-infos {
  position: relative;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

#rz-debug-panel:hover {
  position: fixed;
  padding: 15px;
  bottom: 0;
  left: 0;
  right: 0;
  width: auto;
  height: auto;
  border-radius: 0;
}

#rz-debug-panel:hover:before {
  margin: 0px 5px 10px 0px;
}

#rz-debug-panel:hover ul {
  display: block;
}

/*------------------------------------*   COOKIES CONSENT
\*------------------------------------*/

/*
 * Styles for cookies consent bar
 */

#cookiesconsent .cc_container {
  font-family: "font", Helvetica, Arial, sans-serif;
  padding: 21px 30px 63px;
  opacity: 0.9;
  color: #434343;
  background: #fff;
}

#cookiesconsent .cc_message {
  font-size: 13px;
  line-height: 18px;
  text-align: center;
}

#cookiesconsent .cc_message a {
  color: #ff0000;
}

#cookiesconsent .cc_message a:hover {
  text-decoration: underline;
}

#cookiesconsent .cc_btn {
  position: absolute;
  bottom: 15px;
  left: 50%;
  width: auto;
  max-width: initial;
  margin-left: 0;
  padding: 11px 24px 8px;
  background-color: #ff0000;
  color: #fff;
  border-radius: 34px;
  font: normal 14px/17px "font", Helvetica, Arial, sans-serif;
  float: left;
  transform: translateX(-50%);
  transition: 0.6s background-color cubic-bezier(0.19, 1, 0.22, 1), 0.6s color cubic-bezier(0.19, 1, 0.22, 1);
}

#cookiesconsent .cc_btn:hover {
  background-color: #000;
  color: #ff0000;
}

/*------------------------------------*   BROWSER UPDATE
\*------------------------------------*/

/*
 * Styles for browser update bar
 */

body .buorg {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  border-bottom: none;
  background: #EEEEEE;
  color: #000;
  font-family: "font", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  cursor: default;
}

body .buorg div {
  padding: 12px 20px;
}

body .buorg a,
body .buorg a:visited {
  color: #000;
  text-decoration: none;
  border-bottom: 1px solid #000;
  transition: 0.6s border-bottom-color cubic-bezier(0.19, 1, 0.22, 1);
}

body .buorg a:hover,
body .buorg a:focus,
body .buorg a:visited:hover,
body .buorg a:visited:focus {
  border-bottom-color: transparent;
}

body #buorgclose {
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -7px;
  cursor: pointer;
}

/*------------------------------------*   STYLE ALTERNATIVE
\*------------------------------------*/

/*
 * Styles for old browsers, mobile, no-js, etc.
 */

.no-js #loading {
  display: none;
}

#no-js-message {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 8px 50px;
  font-size: 16px;
  line-height: 19px;
  background: #fff;
  color: #000;
  z-index: 50;
}




